<template>
    <div id="userord">

               <div class="t_top_menu">
            <div class="t_icon">
                <img src="../assets/logo.png" width="150px" height="150px">
            </div>
            <div class="t_search">
                <div class="t_searchinput"><el-input  prefix-icon="el-icon-search"></el-input></div>
                <el-button class="searchBtn" type="primary" icon="el-icon-search">搜索</el-button>
                
                <el-link >新款连衣裙</el-link>&nbsp;
                <el-link > 潮流T恤</el-link>&nbsp;
                <el-link > 时尚女鞋</el-link>&nbsp;
                <el-link > 短裤</el-link>&nbsp;
                <el-link > 半身裙</el-link>&nbsp;
                <el-link > 男士外套</el-link>&nbsp;
                <el-link > 墙纸</el-link>&nbsp;
                <el-link > 行车记录仪</el-link>&nbsp;
                <el-link > 新款男鞋</el-link>&nbsp;
                <el-link > 耳机</el-link>&nbsp;
                <el-link > 时尚女包</el-link>                
            </div>
            <div class="t_user">
                <div class="user_top">
                    <el-image
                            class="imgUserHeader"
                            style="width: 95px; height: 95px"
                            src="./static/2.jpg"
                            :title="username"
                            fit="fill"></el-image>

                </div>
                <div class="user_down">
                    <div class="user_tool">
                        <el-button  icon="el-icon-shopping-cart-2" 
                        title="我的购物车" 
                        @click="enterShopcar"
                        circle></el-button>&nbsp;
                        <el-button  icon="el-icon-notebook-2" 
                        title="我的订单" 
                        @click="enterOrder"
                        circle></el-button>&nbsp;
                        <el-button  icon="el-icon-switch-button" title="注销登录" 
                        @click="quitUser"
                        circle></el-button>
                    </div>
                </div>
            </div>
        </div>




        <div class="midMenu">
            <div class="midmenuL">
            </div>
            <div class="midmenuM">
                
                
                
            </div>
            <div class="midmenuR">
                <ul class="menutext">
                </ul>
            </div>
        </div>

        <div class="direction">
            <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: 'clientHome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>我的订单</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        
        
        <div class="yyy000">
            <div class="ttt00"></div>
            <div class="ttt01">用户:</div>
            <div class="ttt02">{{username}}</div>
            <div class="ttt03">的订单</div>
        </div>

        <div class="seaaa">
            <el-input v-model="ordersearchtext" placeholder="订单搜索" prefix-icon="el-icon-search" class="ordersearch" @input="ordersearch(ordersearchtext)"></el-input>
        </div>
        <!--table-->
        <el-table
            :data="orderlist"
            stripe
            style="width: 100%">
            
            <el-table-column
            
                prop="id"
                label="订单号"
                width="180">
            </el-table-column>
            
            
            <el-table-column
                prop="goodname"
                label="商品名"
                width="180">
            </el-table-column>


            <el-table-column
                prop="count"
                label="数量">
            </el-table-column>

            <el-table-column
                prop="gprice"
                label="单价">
            </el-table-column>


            <el-table-column
                prop="tprice"
                label="总价">
            </el-table-column>

             <el-table-column
                prop="date"
                label="时间">
            </el-table-column>


            
    </el-table>









    </div>
</template>
<script>
export default {
    data() {
        return {
            username: 'haha',
            orderlist: [],
            ordersearchtext: ''
        }
    },
    methods: {
        ordersearch(msg){
            
            this.$http.get('userLikeOrder/'+this.username+'/'+msg).then(res =>{
                
                this.orderlist = res.data;
            })
        },
        quitUser(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("转到登录");
              this.$router.push('login');   
            }else {
                alert("用户:"+window.localStorage.getItem('loginUser')+"即将注销!");
            window.localStorage.clear();
            
            this.$router.push("login");
            this.$message.success("注销成功!");
            }
            
        },
        enterShopcar(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
         
            let routeData = this.$router.resolve({ path: '/clientShopCar', query: {  username: this.username } });
            window.open(routeData.href, '_blank');
        },
        enterOrder(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            this.$router.go(0);
            
            
        }
    },

    created(){
        if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
             
        }

        this.username = window.localStorage.getItem('loginUser');

        this.$http.get('userOrder/'+this.username).then(res=>{
            
            this.orderlist = res.data;
            
        });
    }
}
</script>
<style>
    .ordersearch{
        width: 40%;
        margin-left: 45%;
    }  
    .seaaa{
        width: 60%;
    }
</style>