<template>
    <div id="usersearchGood">
        <div class="t_top_menu">
            <div class="t_icon">
                <img src="../assets/logo.png" width="150px" height="150px">
            </div>
            <div class="t_search">
                <div class="t_searchinput"><el-input  prefix-icon="el-icon-search" v-model="searchmsg"></el-input></div>
                <el-button class="searchBtn" type="primary" icon="el-icon-search" @click="searchGoodsInfo(searchmsg)">搜索</el-button>
                
                <el-link >新款连衣裙</el-link>&nbsp;
                <el-link > 潮流T恤</el-link>&nbsp;
                <el-link > 时尚女鞋</el-link>&nbsp;
                <el-link > 短裤</el-link>&nbsp;
                <el-link > 半身裙</el-link>&nbsp;
                <el-link > 男士外套</el-link>&nbsp;
                <el-link > 墙纸</el-link>&nbsp;
                <el-link > 行车记录仪</el-link>&nbsp;
                <el-link > 新款男鞋</el-link>&nbsp;
                <el-link > 耳机</el-link>&nbsp;
                <el-link > 时尚女包</el-link>                
            </div>
            <div class="t_user">
                <div class="user_top">
                    <el-image
                            class="imgUserHeader"
                            style="width: 95px; height: 95px"
                            src="./static/2.jpg"
                            :title="username"
                            fit="fill"></el-image>

                </div>
                <div class="user_down">
                    <div class="user_tool">
                        <el-button  icon="el-icon-shopping-cart-2" 
                        title="我的购物车" 
                        @click="enterShopcar"
                        circle></el-button>&nbsp;
                        <el-button  icon="el-icon-notebook-2" 
                        title="我的订单" 
                        @click="enterOrder"
                        circle></el-button>&nbsp;
                        <el-button  icon="el-icon-switch-button" title="注销登录" 
                        @click="quitUser"
                        circle></el-button>
                    </div>
                </div>
            </div>
        </div>




        <div class="midMenu">
            <div class="midmenuL">
            </div>
            <div class="midmenuM">
                
                
                
            </div>
            <div class="midmenuR">
                <ul class="menutext">
                </ul>
            </div>
        </div>
        
        
        <div class="direction">
            <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: 'clientHome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>搜索结果</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        
        
        <div class="goodsDiv">
            <div class="good_left"></div>
            <div class="good_right"></div>
            <div class="good" >
                <div class="goodsinfo" >
                    <div class="goodone" 
                    v-for="(item,index) in searchgoodslist" 
                    :key="index"
                    :title="item.gname"
                    @click="checkGood(item.id)">
                        <div class="goodimg">
                            <el-image src="./static/2.jpg" style="width: 100%; height: 100%"></el-image>
                        </div>
                        <div class="goodtext">
                            {{item.gname}}
                        </div>
                        <div class="goodprice">￥{{item.gprice}}</div>
                    </div>
                    
                    
                </div>
            </div>
        </div>
    
    
    
    </div>
</template>
<script>
export default {
    
    data() {
        return {
            searchmsg: '',
            searchgoodslist: []
        }
    },
    methods: {

            checkGood(id1) {
            
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("即将转到登录");
              this.$router.go('login');   
            }
            var f = false;
            this.$http.get('clientGoodInfo/'+id1).then(res=>{
                if(res.data.gname == '00000') {
                    alert('该商品已售罄');
                    f = true; 
                }
                if(f == false) {
                    let routeData = this.$router.resolve({ path: '/clientGoodInfo', query: {  id: id1 } });
                    window.open(routeData.href, '_blank');
                }else {
                    this.$router.go(0);
                }
            
            
            });
            
            
            
            
        },




        searchGoodsInfo(msg){
            
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            
            if(msg == ''){
                return;
            }else {
                // this.$http.get('searchGoods/'+msg).then(res=>{
                //     console.log(res.data);
                // });
                let routeData = this.$router.resolve({ path: '/clientSearchGoods', query: {  searchmsg: msg } });
                  window.open(routeData.href, '_blank');
                // window.open(routeData.href, '_self');
                // this.$router.push(routeData.href);
            }
        },

        quitUser(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("转到登录");
              this.$router.push('login');   
            }else {
                alert("用户:"+window.localStorage.getItem('loginUser')+"即将注销!");
            window.localStorage.clear();
            
            this.$router.push("login");
            this.$message.success("注销成功!");
            }
            
        },
        enterShopcar(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            
            let routeData = this.$router.resolve({ path: '/clientShopCar', query: {  username: this.username } });
            window.open(routeData.href, '_blank');
        },
        enterOrder(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
        
            // this.$router.push('clientUserOrder');
            let routeData = this.$router.resolve({ path: '/clientUserOrder', query: {  username: this.username } });
            window.open(routeData.href, '_blank');
        },
    },


    created(){


         if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
             return;
          }
        this.searchmsg = this.$route.query.searchmsg;

        
        this.$http.get('searchGoods/'+this.searchmsg).then(res=>{
            this.searchgoodslist = res.data;
        });
    }
}
</script>
<style>
    
</style>