<template>
    <div id="goodmsg">
                <div class="t_top_menu">
            <div class="t_icon">
                <img src="../assets/logo.png" width="150px" height="150px">
            </div>
            <div class="t_search">
                <div class="t_searchinput"><el-input  prefix-icon="el-icon-search"></el-input></div>
                <el-button class="searchBtn" type="primary" icon="el-icon-search">搜索</el-button>
                
                <el-link >新款连衣裙</el-link>&nbsp;
                <el-link > 潮流T恤</el-link>&nbsp;
                <el-link > 时尚女鞋</el-link>&nbsp;
                <el-link > 短裤</el-link>&nbsp;
                <el-link > 半身裙</el-link>&nbsp;
                <el-link > 男士外套</el-link>&nbsp;
                <el-link > 墙纸</el-link>&nbsp;
                <el-link > 行车记录仪</el-link>&nbsp;
                <el-link > 新款男鞋</el-link>&nbsp;
                <el-link > 耳机</el-link>&nbsp;
                <el-link > 时尚女包</el-link>                
