                </div>
                <div class="user_down">
                    <div class="user_tool">
                        <el-button  icon="el-icon-shopping-cart-2" 
                        title="我的购物车" 
                        @click="enterShopcar"
                        circle></el-button>&nbsp;
                        <el-button  icon="el-icon-notebook-2" 
                        title="我的订单" 
                        @click="enterOrder"
                        circle></el-button>&nbsp;
                        <el-button  icon="el-icon-switch-button" title="注销登录" 
                        @click="quitUser"
                        circle></el-button>
                    </div>
                </div>
            </div>
        </div>




        <div class="midMenu">
            <div class="midmenuL">
            </div>
            <div class="midmenuM">
                
                
                
            </div>
            <div class="midmenuR">
                <ul class="menutext">
                </ul>
            </div>
        </div>

        <div class="direction">
            <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: 'clientHome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品详情</el-breadcrumb-item>
            </el-breadcrumb>
        </div>


        
        
        
        
        
        
        
        
        <div class="midInfo">
            <div class="goodplace_left">
            </div>
            <div class="goodplace">
                
                <div class="goodimgplace">
                    <div class="see_img">
                        <el-image
                        src="./static/2.jpg"
                        style="height: 432px">

                        </el-image>
                    </div>
                    <div class="set_img"></div>
                </div>

                <div class="goodinfoplace">
                    <div class="goodtitle">
                        {{goodinfo.gname}}
                    </div>
                    
                    <div class="goodmessage">
                        <div class="goodprice1_1">库存:</div>
                        <div class="goodprice1_8">{{goodinfo.gcount}}</div>
                        
                    </div>
                    
                    
                    <div class="goodselect">
                        <div class="goodprice1_3">数量:</div>
                        <div class="goodprice1_4">
                            <el-input-number v-model="goodnum" @change="handleChange" :min="1" :max="goodinfo.gcount" ></el-input-number>
                        </div>
                        
                        
                        
                    </div>
                    
                    
                    
                    <div class="goodprice1">
                        <div class="goodprice1_1">价格:</div>
                        <div class="goodprice1_2">￥{{goodinfo.gprice}}</div>
                    </div>
                    <div class="goodmessage">
                        <div class="goodprice1_1">运费:</div>
                        <div class="goodprice1_2">￥10</div>
                    </div>

                    <div class="goodprice1">
                        <div class="goodprice1_1">总价:</div>
                        <div class="goodprice1_2">￥{{totalprice}}</div>
                    </div>

                    <div class="goodbuy">
                        <div class="buythis">
                            <el-button type="danger" @click="butitnow()">立即购买</el-button>
                        </div>
                        <div class="addthis">
                            <el-button type="danger" icon="el-icon-shopping-cart-full" @click="addShopCart()">添加购物车</el-button>
                        </div>
                    </div>

