                        <div class="goodprice1_5">服务承诺:</div>
                        <li class="select_li5">意外保修</li>
                        <li class="select_li5">正品保证</li>
                        <li class="select_li5">全面保修</li>
                        <li class="select_li5">延保服务</li>
                        <li class="select_li5">急速退款</li>
                        <li class="select_li5">退货运险费</li>
                        <li class="select_li5">七天无理由退换</li>
                    </div>
                </div>

            </div>
            <div class="goodplace_right">
            </div>
        </div>



        
    </div>
</template>
<script>
export default {
    data() {
        return {
            goodinfo: {},
            id: -1,
            goodnum: 1,
            totalprice: 1,
            username: '',
            shopgood: {
                sid: -1,
                gname: '',
                date:'',
                gcount: -1,
                ttcount:-1,
                gprice: -1,
                tprice: -1,
            },
            

        }
    },
    methods:{
        handleChange(value) {

            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              
            }else {
                this.totalprice = 10+value*this.goodinfo.gprice;
            }
            
      },

        butitnow(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              
            }else {
                var ttcout = -1;
                this.$http.get('clientGoodInfo/'+this.id).then(res=>{
                    if(res.data.gname == '00000') {
                        alert("该商品售罄!");
                        this.$router.push('clientHome');
                    }else{
                        //---------------------
                        if(res.data.gcount < this.goodnum){
                            alert("库存不够");
                            this.$router.go(0);
                        }else{
                            let list = JSON.parse(localStorage.getItem('shopcart'));
                            for(var i =0;i<list.length;i++){
                                if(list[i].sid == this.id){
                                    if(res.data.gcount == this.goodnum){
                                        list.splice(i,1);
                                        window.localStorage.setItem('shopcart',JSON.stringify(list));
                                    }
                                    
                                }
                            }
                            ttcout = res.data.gcount-this.goodnum;
                            alert("购买成功!已生成订单");
                            if(ttcout != -1){
                                this.$http.get('updateGoodsCount/'+this.id+'/'+ttcout).then(res1 =>{
                                    let year = new Date().getFullYear();//年
                                    let month = new Date().getMonth() + 1;
                                    let day = new Date().getDate();//日 
                                    let hh = new Date().getHours(); //时
                                　　let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();//分
                                    
                                    
                                    var order = {
                                        username : this.username,
                                        goodname : this.goodinfo.gname,
                                        count : this.goodnum,
                                        gprice :this.goodinfo.gprice,
                                        tprice :this.totalprice,
                                        date: year+"-"+month+"-"+day+" "+hh+":"+mf
                                    };
                                    this.$http.post('saveOrder',order).then(res=>{
                                        
                                        this.$router.go(0);
                                    });
                                    
                                });
                            }
                        }


                        //---------------------
                    }
                })
                
                
            
            
            
            
            
            
            
            }
        },


      addShopCart(){
          if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
           }
            var f2 = true;
            this.id = this.$route.query.id;
            this.$http.get('clientGoodInfo/'+this.id).then(res=>{
                if(res.data.gname == '00000') {
                    alert("该商品售罄!");
                    f2 = false;
                    this.$router.push('clientHome');
                    
                }else {
                        if(f2 == true){
                            if(f2 == true) {
                    
                    if(this.goodinfo == ''){
                alert("error");
                this.$router.push('clientHome');
            }else {
                
                    this.shopgood.sid = this.id;
                    this.shopgood.gname = this.goodinfo.gname;
                    this.shopgood.gcount = this.goodnum;
                    this.shopgood.ttcount = this.goodinfo.gcount;
                    this.shopgood.gprice = this.goodinfo.gprice;
                    this.shopgood.tprice = this.totalprice;
                        let list = JSON.parse(localStorage.getItem('shopcart'));
                        var bool = false;
                    let kid = -1;
                    list.forEach(element => {
                        if(this.shopgood.sid == element.sid) {
                            kid = element.sid;
                            element.gcount += this.goodnum;
                            element.tprice += this.totalprice-10;
                            bool = true;
                        }
                    });
                    if(bool == true) {
                        this.$message.success("您的购物车有重复");
                        var f1 = false;
                        let l1 = JSON.parse(localStorage.getItem('shopcart'));
                        l1.forEach(k => {
                            if(k.sid == kid) {
                            console.log("gcount = "+k.gcount + "::ttcount="+k.ttcount);
                                if(k.gcount+this.goodnum > k.ttcount) {
                                    
                                    f1 = true;
                                    return;
                                }
                            }
                        });
                        if(f1 == false) {
                            this.$message.success("添加成功！");
                            window.localStorage.setItem('shopcart',JSON.stringify(list));
                        }
                        
                        
                        
                        
                    }else {
                        this.$message.success("添加成功!");
                        let year = new Date().getFullYear();//年
                            let month = new Date().getMonth() + 1;
                            let day = new Date().getDate();//日 
                            let hh = new Date().getHours(); //时
                        　　let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();//分
                            this.shopgood.date = year+"-"+month+"-"+day+" "+hh+":"+mf;

                            
                            list.push(this.shopgood)
                            window.localStorage.setItem('shopcart',JSON.stringify(list));
                            console.log(list);
                    }
                }


            
            }
                    }
                }
            });

            
          
          
          
          

        
        
          

      },
      quitUser(){
          
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("转到登录");
              this.$router.push('login');   
            }else {
                alert("用户:"+window.localStorage.getItem('loginUser')+"即将注销!");
                window.localStorage.clear();
                
                this.$router.push("login");
                this.$message.success("注销成功!");
            }
            
            
            
            
            
      },
      enterOrder(){
          
          if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            // this.$router.push('clientUserOrder');
            let routeData = this.$router.resolve({ path: '/clientUserOrder', query: {  username: this.username } });
            window.open(routeData.href, '_blank');
      },
      enterShopcar(){
          
         if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            // this.$router.push('clientShopCar');
            let routeData = this.$router.resolve({ path: '/clientShopCar', query: {  username: this.username } });
            window.open(routeData.href, '_blank');
      }
    },

    created(){

        if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
             return;
          }
        // alert("参数id="+this.$route.query.id);
        this.id = this.$route.query.id;
        this.$http.get('clientGoodInfo/'+this.id).then(res=>{
            if(res.data.gname == '00000') {
                alert("该商品售罄!");
                this.$router.push('clientHome');
            }else {
                this.goodinfo = res.data;
                var p = res.data.gprice;
                this.totalprice = p+10;
                this.username = window.localStorage.getItem('loginUser');
            }
            
            
        });
    }
}
</script>
