<template>
    <div class="prefix">
        
        <router-link to="login" >
            <el-button class="bt" :type="type1" @click="pressLog">登录</el-button>
        </router-link>
        
        <router-link to="register">
            <el-button class="bt" :type="type2" @click="pressReg">注册</el-button>
        </router-link>

        <router-view class="comp"></router-view>
    </div>
</template>
<script>
export default {
    data() {
        return {
            type1: 'primary',
            type2: '',
        }
    },
    methods: {
        pressLog() {
            this.type1 = 'primary';
            this.type2 = '';
        },
        pressReg() {
            this.type2 = 'primary';
            this.type1 = '';
        }
    },
}
</script>
<style>
    .prefix{
        width: 500px;
        height: 440px;
        border: blue 1px solid;
        
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    
    .bt{
        width: 50%;
        
    }
    
</style>