<template>
    <div class="login">
        <div class="login_head">
            <h1>用户登录</h1>
        </div>
        
        <div class="login_box">
            <el-form :model="login_form" class="form" ref="login_form">
                <el-form-item >
                    <el-input v-model="login_form.username" 
                    prefix-icon="el-icon-user"
                    placeholder="请输入用户名"
                    ></el-input>
                </el-form-item>

                <el-form-item >
                    <el-input v-model="login_form.password" 
                    prefix-icon="el-icon-lock"
                    placeholder="请输入密码"
                    ></el-input>
                </el-form-item>
            </el-form>
            
            <div class="login_btn">
                <el-button type="info" @click="reset">重置</el-button>
                <el-button type="primary" @click="login">登录</el-button>
            </div>
        
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            login_form: {
            username: 'root',
            password: 'root',
            },
            shopcar: []
        }
    },
    methods: {
        login() {
            if(this.login_form.username === '' || this.login_form.username == '') {
                this.$message.error("请填写完整信息!");
                return false;
            }
            const _this = this;
            this.$http.post('login',this.login_form).then(function(resp){
                console.log(resp.data.loginMsg);
                if(resp.data.loginMsg == "ok") {
                    if(resp.data.rootUser == 'yes') {
                        _this.$message.success("管理员登录成功~");
                        window.localStorage.setItem("loginUser1",resp.data.username);
                        _this.$router.push("/home");
                    }else {
                        _this.$message.success("登录成功~");
                        window.localStorage.setItem("loginUser",resp.data.username);
                        window.localStorage.setItem("shopcart",JSON.stringify(_this.shopcar));
                        _this.$router.push("/clientHome");
                    }
                    
                    
                    
                }else {
                    _this.$message.error("登录失败~");
                }
            });
        },
        reset() {
            this.login_form = {};
        },
    },

    created(){
        if(window.localStorage.getItem('loginUser') != null) {
            this.$message.success("已有登录信息!,跳转至主页");
            this.$router.push('clientHome');
        }else {
            
        }
    }
    
}
</script>

<style>
    .login{
        width: 500px;
        height: 400px;
        border: solid 1px blue;
        
    }
    .login_head{
        margin-top: 30px;
        margin-left: 30px;
    }
    .form{
        width: 100%;
        padding: 30px;
        box-sizing: border-box; 
    }
    .login_btn{
        float: right;
        margin-right: 30px;
    }
    
</style>