<template>
    <div class="main">
        <div class="body_menu_user">
            商品记录
        </div>

        <div class="search">
            <el-input class="searchInput" 
            v-model="searchText"
            placeholder="输入商品名搜索"
            prefix-icon="el-icon-search"
            @input="searchGood(searchText)">
            </el-input>
        </div>

        
        <el-table
            :data="arry"
            stripe
            style="width: 100%">
            
            <el-table-column
            
                prop="id"
                label="编号"
                width="180">
            </el-table-column>
            
            
            <el-table-column
                prop="gname"
                label="商品名"
                width="180">
            </el-table-column>


            <el-table-column
                prop="gcount"
                label="库存">
            </el-table-column>

            <el-table-column
                prop="gprice"
                label="单价">
            </el-table-column>


            <el-table-column
                prop="sales"
                label="销量">
            </el-table-column>

             <el-table-column label="已上架">
                <template slot-scope="scope">
                    <el-switch
                        v-model="scope.row.state"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        @change="changeState(scope.row.id)">
                    </el-switch>
                    </template>
            </el-table-column>

            <el-table-column label="移除">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
            </el-table-column>

            
    </el-table>
    <div class="block">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div>

    </div>
</template>
<script>
export default {
    data() {
        return {
            arry: [],
            searchText: '',
            currentPage: 1,
            total: 1,
            pageSize: 1,
            
        }
    },
    methods: {
        
        
        
            
            //删除商品
            handleDelete(index, row) {
                console.log("即将删除:"+row.id);
                this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        this.$http.get('delGood/'+row.id).then(res =>{
                        if(res.data == 'ok') {
                            this.$message.success('删除成功!');
                            this.$router.go(0);
                        }
                        });
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                    });
                
            },

        handleSizeChange(val) {
            
        },
        handleCurrentChange(val) {
            this.$http.get('goodinfo/'+this.pageSize+'/'+val).then(res => {
            
                console.log(res.data);
                this.currentPage = res.data.currentPage;
                this.total = res.data.total;
                this.pageSize = res.data.pageSize;
                this.arry = res.data.slist;
                
                
            })
        },
        //搜索商品
            searchGood(text){
                console.log("搜索"+text);
                this.$http.get('goodFindLike/'+text).then(res =>{
                    this.currentPage = res.data.currentPage;
                    this.total = res.data.total;
                    this.pageSize = res.data.pageSize;
                    this.arry = res.data.slist;
                    
                })

            },

            //改变状态
            changeState(id) {
                
                this.$http.get('changeState/'+id).then(res=>{
                    console.log(res.data);
                });
            }
        
        
    },
    //点开页面获取第一页数据
    created(){
        
        this.$http.get('goodinfo/10/1').then(res => {
            
            console.log(res.data);
            this.currentPage = res.data.currentPage;
            this.total = res.data.total;
            this.pageSize = res.data.pageSize;
            this.arry = res.data.slist;
            
           
            
        }).catch(err => {
    	    console.log(err);
        });

         
    }
}
</script>
<style>
    .main{
        width: 100%;
        min-width: 600px;
    }
    
    
</style>