<template>
    <div class="register">
        
            
                <div class="register_head">
                    <h1>用户注册</h1>
                </div>
            
            
            
            <el-form :model="register_form" class="register_form" ref="register_form">
                <el-form-item >
                    <el-input v-model="register_form.username" 
                    prefix-icon="el-icon-user"
                    placeholder="请输入用户名"></el-input>
                </el-form-item>

                <el-form-item >
                    <el-input v-model="register_form.password" 
                    prefix-icon="el-icon-lock"
                    placeholder="请输入密码"></el-input>
                </el-form-item>

                <el-form-item >
                    <el-input v-model="register_form.password2" 
                    prefix-icon="el-icon-lock"
                    placeholder="请确认密码"></el-input>
                </el-form-item>
            </el-form>
            
            <div class="register_btn">
                <el-button type="info" @click="reset">重置</el-button>
                <el-button type="primary" @click="register">注册</el-button>
            </div>
            
    </div>
</template>

<script>
export default {
    data() {
        return {
            register_form: {
                username: '',
                password: '',
                password2: '',
            },
        }
    },
    methods: {
        reset() {
            this.register_form={};
        },
        register() {
            var patt = /^\w{3,10}$/;
            if(this.register_form.username === '' || this.register_form.username == '') {
                this.$message.error("请填写完整信息!");
                return false;
            }
            if(!patt.test(this.register_form.username)) {
                
                this.$message.error("用户名不符!(3~10字符)");
                return false;
            }
            if(!patt.test(this.register_form.password)) {
                
                this.$message.error("密码不符!(3~10字符)");
                return false;
            }
            if(this.register_form.password != this.register_form.password2) {
                
                this.$message.error("两次密码不一致!");
                return false;
            }
            this.$http.post('register',this.register_form).then(res=>{
                if(res.data.registerInfo == 'ok') {
                    this.$message.success("注册成功~现在去登陆吧!");
                    
                }else {
                    this.$message.error("注册失败! 用户名重复");
                }
            });
        }
    },
}
</script>

<style>
    .register{
        width: 500px;
        height: 400px;
        border: solid 1px blue;
        
    }
    .register_head{
        margin-top: 30px;
        margin-left: 30px;
    }
    .register_form{
        width: 100%;
        padding: 30px;
        box-sizing: border-box;
    }
    .register_btn{
        float: right;
        margin-right: 30px;
        
    }

    
    
    
    

</style>