<template>
        <div class="mainorder">
        <div class="body_menu_user">
            订单管理
        </div>

        <div class="search">
            <el-input class="searchInput" 
            v-model="searchText"
            placeholder="订单搜索"
            prefix-icon="el-icon-search"
            @input="searchOrder(searchText)">
            </el-input>
        </div>

        
        <el-table
            :data="orderList"
            stripe
            style="width: 100%">
            
            <el-table-column
            
                prop="id"
                label="订单号"
                width="180">
            </el-table-column>
            
            
            <el-table-column
                prop="username"
                label="用户名"
                width="180">
            </el-table-column>


            <el-table-column
                prop="goodname"
                label="商品名">
            </el-table-column>

            <el-table-column
                prop="gprice"
                label="商品单价">
            </el-table-column>
            <el-table-column
                prop="count"
                label="数量">
            </el-table-column>
            <el-table-column
                prop="tprice"
                label="总价">
            </el-table-column>

            

             

           

            
    </el-table>
    <div class="block">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div>

    </div>
</template>
<script>
export default {
    data() {
        return {
            orderList: [],
            searchText: '',
            currentPage: 1,
            total: 1,
            pageSize: 1,
        }
    },
    methods: {

        searchOrder(uname){
            this.$http.get('findLikeUsername/'+uname).then(res => {
            
                console.log(res.data);
                this.currentPage = res.data.currentPage;
                this.total = res.data.total;
                this.pageSize = res.data.pageSize;
                this.orderList = res.data.slist;
                
                
            })
        },
        
        handleSizeChange(val) {
            
        },
        handleCurrentChange(val) {
            this.$http.get('allOrder/'+this.pageSize+'/'+val).then(res => {
            
                console.log(res.data);
                this.currentPage = res.data.currentPage;
                this.total = res.data.total;
                this.pageSize = res.data.pageSize;
                this.orderList = res.data.slist;
                
                
            })
        },
    },  


    created(){
        this.$http.get('allOrder/'+10+'/'+1).then(res=>{
            this.orderList = res.data.slist;
            this.currentPage = res.data.currentPage;
            this.total = res.data.total;
            this.pageSize = res.data.pageSize;
            
        });
    }
}
</script>
<style>
    
</style>