    :data="shopcart"
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
      
      label="商品编号"

      width="120">
      <template slot-scope="scope">
         {{scope.row.sid}}
      </template>
      <!-- <template slot-scope="scope">{{ scope.row.date }}</template> -->
    </el-table-column>
    <el-table-column
      prop="gname"
      label="商品名称"
      width="120">
    </el-table-column>
    <el-table-column
      prop="date"
      label="添加时间"
      show-overflow-tooltip>
    </el-table-column>

    <el-table-column
      
      label="单价"
      show-overflow-tooltip>
      <template slot-scope="scope">
         {{scope.row.gprice}}
      </template>
    </el-table-column>

    <el-table-column
      
      label="库存"
      show-overflow-tooltip>
      <template slot-scope="scope">
         {{scope.row.ttcount}}
      </template>
    </el-table-column>

    <el-table-column
      
      label="数量"
      show-overflow-tooltip>
      <template slot-scope="scope">
          <el-input-number size="mini" v-model="scope.row.gcount" @change="handleChange(scope.row.sid, scope.row.gcount)"  :min="1" :max="scope.row.ttcount" ></el-input-number>
      </template>
    </el-table-column>

    <el-table-column
      label="总价"
      show-overflow-tooltip>
      <template slot-scope="scope">
         {{scope.row.tprice}}
      </template>
    </el-table-column>

