</template>
<script>
export default {
    data() {
        return {
            username: '',
            shopcart: [
                
            ],
            gcount:0,

            ttprice:0,
            ttcount:0,

            selectgoods:[],
            // //更新shopcart方法
            // updateShopcart: function(){
            //         this.shopcart.forEach(element => {
            //             this.$http.get('clientGoodInfo/'+element.sid).then(res=>{
            //                 if(element.sid == res.data.id){
                                
            //                     element.ttcount = res.data.gcount;
            //                     alert("hh"+element.ttcount);
            //                 }
            //             })
            //         });
            //         window.localStorage.setItem('shopcart',JSON.stringify(this.shopcart));
            //         return true;
            // },
            // //判断数据方法
            // isDataValid(){
            // var flag = true;
            //     this.selectgoods.forEach(element => {
            //         this.$http.get('clientGoodInfo/'+element.sid).then(res=>{
            //             if(res.data.gname == '00000' || res.data.gcount < element.gcount){
            //                 flag = false;
            //             } 
            //         })
            //     });
            //     return flag;
            // },



        }
    },
    methods: {

        delShopcar(sid){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            var delid = -1;
            console.log("删除之前:"+this.shopcart);
            for(var i = 0 ;i <this.shopcart.length ;i++) {
                if(this.shopcart[i].sid == sid) {
                    
                    delid = i;
                    
                }
            }
            this.shopcart.splice(delid,1);
            console.log("删除之后:"+this.shopcart);
            let list = this.shopcart;
            window.localStorage.setItem('shopcart',JSON.stringify(list));

        },
        handleChange(sid,count){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            var tpnow = 0;
            this.shopcart.forEach(element => {
                if(element.sid == sid) {
                    element.tprice = 10+element.gprice*count;
                    tpnow = element.tprice;
                }
            });


            
            var boo = false;
            this.selectgoods.forEach(element => {
                if(element.sid == sid) {
                    boo = true;
                    element.tprice = tpnow;
                }
            });
            
            if(boo) {
                this.ttprice = 0;
                this.selectgoods.forEach(element => {
                    this.ttprice += element.tprice;
                });
            }
            
            
            let list = this.shopcart;
            window.localStorage.setItem('shopcart',JSON.stringify(list));

            

            
        },
        toggleSelection(rows) {
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            if (rows) {
            rows.forEach(row => {
                this.$refs.multipleTable.toggleRowSelection(row);
                
            });
            } else {
            this.$refs.multipleTable.clearSelection();
            }
      },
      handleSelectionChange(val) {
          if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
          this.selectgoods = val;
        console.log(val);
        this.ttcount = val.length;
        this.ttprice = 0;
        val.forEach(element => {
            this.ttprice += element.tprice;
        });
        this.multipleSelection = val;
      },


      quitUser(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("转到登录");
              this.$router.push('login');   
            }else {
                alert("用户:"+window.localStorage.getItem('loginUser')+"即将注销!");
            window.localStorage.clear();
            
            this.$router.push("login");
            this.$message.success("注销成功!");
            }
            
        },
        enterShopcar(){
            
         if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            this.$router.go(0);
        },
        enterOrder(){
            
         if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            // this.$router.push('clientUserOrder');
            let routeData = this.$router.resolve({ path: '/clientUserOrder', query: {  username: this.username } });
            window.open(routeData.href, '_blank');
        },


        // async getScheduleList(selectDate) {
        //     let response;
        //     // 这里request为向服务的发请求的方法
        //     await request(api.getScheduleList, {
        //         date: selectDate
        //     }).then(res => {
        //         response = res;
        //     });
        //     return response
        // },

        // init() {
        //     this.getScheduleList(selectDate).then(res => {
        //         console.log(res)
        //     })
        // }
        
        async isDataValid(){
            var flag = true;
            this.selectgoods.forEach(element => {
                 this.$http.get('clientGoodInfo/'+element.sid).then(res=>{
                    if(res.data.gname == '00000' || res.data.gcount < element.gcount){
                        alert(element.gname+"库存不足");
                    }else {
                        var recount = res.data.gcount;
                        this.$http.get('updateGoodsCount/'+element.sid+'/'+(recount - element.gcount)).then(res=>{
                            
                        });
                    }
                })
            });
            
            return flag;
        },
            //正常处理数据
        dealdata(){
            var idList = [];
            this.shopcart.forEach(sc => {
                this.selectgoods.forEach(sg => {
                    if(sc.sid == sg.sid){
                        idList.push(sc.sid);
                        if(sc.ttcount == sg.gcount){
                            sc.ttcount = 0;
                            sc.gcount = 0;
                            sc.tprice = 0;
                        }else  {
                            sc.ttcount -= sg.gcount;
                            sc.gcount = 1;
                            sc.tprice = sc.gprice;
                        }
                    }
                });
            });
            window.localStorage.setItem('shopcart',JSON.stringify(this.shopcart));
        },
       
       
       //更新shopcart
       async updateShopcart(that){
           await that.shopcart.forEach(element => {
                that.$http.get('clientGoodInfo/'+element.sid).then(res=>{
                    if(element.sid == res.data.id){
                        
                        element.ttcount = res.data.gcount;
                        alert(element.ttcount);
                    }
                });
            });
            
            window.localStorage.setItem('shopcart',JSON.stringify(that.shopcart));
            return false;
        },

        
        
        //结算/??????????????????????????????????????????????????????????????????????????????????
        overbuy: async function(){
           if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            if(this.selectgoods == '' ) {
                this.$message.error("还未勾选商品!");
                
            }else {
                // const _this = this;
                // const f = await this.$options.methods.updateShopcart(_this);
                // if(f == true){
                //     alert("hh"+123);
                // }
                this.selectgoods.forEach(element => {
                    this.$http.get('clientGoodInfo/'+element.sid).then(res1=>{
                        if(res1.data.gname == '00000' || res1.data.gcount < element.gcount){
                            alert(res1.data.gname+"库存不足");
                            this.$router.go(0);
                            return;
                        }else {
                            var recount = res1.data.gcount;
                            let now =(recount - element.gcount);
                            
                            this.$http.get('updateGoodsCount/'+element.sid+'/'+now).then(res2=>{
                                if(res2.data == 'delok'){
                                    // this.shopcart.forEach(sc => {
                                    //     if(res1.data.id == sc.sid){
                                    //         sc.gcount = 0;
                                    //         sc.ttcount = 0;
                                    //         sc.tprice = 0;
                                    //     }
                                    // });

                                    //寻找待删除的商品
                                    let delList = [];
                                    for(let i = 0 ; i<this.shopcart.length ;i++){
                                        if(res1.data.id == this.shopcart[i].sid){
                                            delList.push(i);
                                        }
                                    }
                                    //遍历删除
                                    var m = 0;
                                    delList.forEach(element => {
                                        this.shopcart.splice(element-m,0);
                                        m++;
                                    });

                                    
                                    window.localStorage.setItem('shopcart',JSON.stringify(this.shopcart));
                                    alert("购买成功");
                                    let year = new Date().getFullYear();//年
                                    let month = new Date().getMonth() + 1;
                                    let day = new Date().getDate();//日 
                                    let hh = new Date().getHours(); //时
                                　　let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();//分
                                    
                                    var order = {
                                        username : this.username,
                                        goodname : element.gname,
                                        count : element.gcount,
                                        gprice :element.gprice,
                                        tprice :element.tprice,
                                        date: year+"-"+month+"-"+day+" "+hh+":"+mf
                                    };
                                    this.$http.post('saveOrder',order).then(res=>{
                                        
                                         this.$router.go(0);
                                    });
                                    
                                }else {
                                    
                                    this.shopcart.forEach(sc => {
                                        if(res1.data.id == sc.sid){
                                            sc.gcount = 1;
                                            sc.ttcount = now;
                                            sc.tprice = sc.gprice;
                                            window.localStorage.setItem('shopcart',JSON.stringify(this.shopcart));
                                        }
                                    });
                                    alert("购买成功");
                                    let year = new Date().getFullYear();//年
                                    let month = new Date().getMonth() + 1;
                                    let day = new Date().getDate();//日 
                                    let hh = new Date().getHours(); //时
                                　　let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();//分
                                    
                                    var order = {
                                        username : this.username,
                                        goodname : element.gname,
                                        count : element.gcount,
                                        gprice :element.gprice,
                                        tprice :element.tprice,
                                        date: year+"-"+month+"-"+day+" "+hh+":"+mf
                                    };
                                    this.$http.post('saveOrder',order).then(res=>{
                                        alert(res.data);
                                         this.$router.go(0);
                                    });
                                    
                                }
                                 
                            });
                        }
                    })
                });
            }
        }
    },

    created() {

        if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
             
          }
        this.username = this.$route.query.username;
        
        let list = JSON.parse(localStorage.getItem('shopcart'));
        let delList = [];
        var m =0;
        for(let i=0;i<list.length;i++){
            this.$http.get('clientGoodInfo/'+list[i].sid).then(res=>{
                if(res.data.gname == '00000'){
                    delList.push(i);
                    list.splice(i-m,1);
                    m++;
                    
                    window.localStorage.setItem('shopcart',JSON.stringify(list));
                    this.shopcart = JSON.parse(localStorage.getItem('shopcart'));
                }else {
                    if(list[i].sid == res.data.id){
                        list[i].ttcount = res.data.gcount;
                        list[i].gcount = 1;
                        list[i].tprice = res.data.gprice;
                        window.localStorage.setItem('shopcart',JSON.stringify(list));
                        this.shopcart = JSON.parse(localStorage.getItem('shopcart'));
                    }
                }
            })
        }
        
        
    }
}
</script>
