<template>
    <div id="goodMannger">
        <h1>商品管理</h1>
        
        
                <div class="addGoodDiv">
                    <h2>新增商品</h2>
                    <div class="addGoodForm">
                        <el-form  label-width="80px" :model="addForm">
                            <el-form-item label="名称">
                                <el-input v-model="addForm.gName" placeholder="请输入商品名称"></el-input>
                            </el-form-item>
                            <el-form-item label="库存">
                                <el-input v-model="addForm.gCount" placeholder="请输入商品数量"></el-input>
                            </el-form-item>
                            <el-form-item label="价格">
                                <el-input v-model="addForm.gPrice" placeholder="请输入商品单价"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="addGood(addForm)">提交</el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                </div>

            <div class="updGoodDiv">
                <h2>更新商品信息</h2>
                <div>
                    <el-form  label-width="80px" :model="updateForm">
                        <el-form-item label="编号">
                            <el-input v-model="updateForm.id" placeholder="请输入待更新商品的编号"></el-input>
                        </el-form-item>

                    <el-form-item label="名称">
                        <el-input v-model="updateForm.gName" placeholder="请输入更新后的商品名称"></el-input>
                    </el-form-item>
                    <el-form-item label="库存">
                        <el-input v-model="updateForm.gCount" placeholder="请输入更新后的商品数量"></el-input>
                    </el-form-item>
                    <el-form-item label="价格">
                        <el-input v-model="updateForm.gPrice" placeholder="请输入更新后的商品单价"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="warning" @click="updateGood(updateForm)">提交</el-button>
                    </el-form-item>
                    </el-form>
                </div>
            </div>
    

        
    </div>
</template>
<script>
export default {
    
    data() {
        return {
            addForm: {
                gName: '',
                gCount: '',
                gPrice: '',
            },
            updateForm: {
                id: '',
                gName: '',
                gCount: '',
                gPrice: '',
            }
        }
    },
    methods: {
        addGood(goodInfo){
            

             this.$http.post('addGood',goodInfo).then(res=>{
                if(res.data == 'ok') {
                    this.$message.success("添加成功~");
                    
                }else {
                    this.$message.error("添加失败");
                }
            });
        },

        updateGood(goodInfo){
            

             this.$http.post('updateGood',goodInfo).then(res=>{
                if(res.data == 'ok') {
                    this.$message.success("修改成功~");
                    
                }else {
                    this.$message.error("修改失败");
                }
            });
        },
    }
}
</script>
<style>
    
    .addGoodDiv{
        width: 400px;
        height: 350px;
        float: left;
        margin-top: 100px;
        margin-left: 100px;
        padding: 10px;
        box-sizing: border-box;
        border: solid 2px blue;
        border-radius: 20px;
    }
    
    


    .updGoodDiv{
        width: 380px;
        height: 400px;
        float: right;
        margin-top: 100px;
        border: solid 2px orange;
        padding: 10px;
        box-sizing: border-box;
        margin-right: 100px;
        border-radius: 20px;
    }

    
</style>