<template>
    <div>
        <div class="body_menu_user">
            用户记录
        </div>

        <div class="search">
            <el-input class="searchInput" 
            v-model="searchText"
            placeholder="输入用户名搜索"
            prefix-icon="el-icon-search"
            @input="searchUser(searchText)">
            </el-input>
        </div>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            
            <el-table-column
            
                prop="id"
                label="编号"
                width="180">
            </el-table-column>
            
            
            <el-table-column
                prop="username"
                label="用户名"
                width="180">
            </el-table-column>


            <el-table-column
                prop="password"
                label="密码">
            </el-table-column>

             <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
            </el-table-column>

            
    </el-table>
    <div class="block">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div>  
    </div>
</template>
<script>
export default {
    data() {
        return {
            tableData: [
                
            ],
            searchText: '',
            currentPage: 1,
            total: 1,
            pageSize: 1,
            sendFlag: false,
             
            
        }
    },
    methods: {

            //编辑删除用户
            handleEdit(index, row) {
                console.log(index, row);
                
                this.$prompt('请输入修改后的密码', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                
                }).then(({ value }) => {
                    this.$http.get('updateUser/'+row.id+'/'+row.username+'/'+value).then(res=>{
                        this.$router.go(0);
                    });
                    this.$message({
                        type: 'success',
                        message: '你的现在的密码是: ' + value
                    });
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消输入'
                });       
                });


            },
            handleDelete(index, row) {
                console.log("即将删除:"+row.id);
                this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        this.$http.get('delUser/'+row.id).then(res =>{
                        if(res.data == 'ok') {
                            this.$message.success('删除成功!');
                            this.$router.go(0);
                        }
                        });
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                    });
                
            },

            //搜索用户
            searchUser(text){
                console.log("搜索"+text);
                this.$http.get('userFindLike/'+text).then(res =>{
                    this.currentPage = res.data.currentPage;
                    this.total = res.data.total;
                    this.pageSize = res.data.pageSize;
                    this.tableData = res.data.slist;
            
                })

            },

        //页面变化
        handleSizeChange(val) {
            
        },
        handleCurrentChange(val) {
            if(!this.sendFlag){
                this.$http.get('userinfo/'+this.pageSize+'/'+val).then(res =>{
                this.currentPage = res.data.currentPage;
                this.total = res.data.total;
                this.pageSize = res.data.pageSize;
                this.tableData = res.data.slist;
            
            })
            }
        }
        
        },
    created(){
        
        // this.$http.get('').then(res=>{

        // }).catch(err=>{

        // })


        //创建时读取数据
        this.$http.get('userinfo/10/1').then(res => {
            
            console.log(res.data);
            this.currentPage = res.data.currentPage;
            this.total = res.data.total;
            this.pageSize = res.data.pageSize;
            this.tableData = res.data.slist;
            
        }).catch(err => {
    	    console.log(err);
        });

         
    }
}
</script>
<style>
    .body_menu_user{
        
        float: left;
        font-size: 30px;
        font-weight: bold;
        background: rgb(228, 228, 228);
  
    }
    
    .search{
        padding-top: 10px;
        margin-right: 20px;
        height: 50px;
        width: 400px;
        float: right;
        
    }
    .block{
        background-color: rgb(228, 230, 230);
    }
</style>