<script>
export default {
    data() {
        return {
            imgArr: [
                "./static/tb01.jpg",
                './static/tb02.jpg',
                './static/tb03.jpg',
            ],
            imgArr2: [
                "./static/tb04.png",
                './static/tb05.png',
                './static/tb06.png',
            ],

            goodArr: [
                
            ],
            username: '',
            searchtext: '',
        }
    },
    methods: {



        searchGoodsInfo(msg){
            
            


            if(msg == ''){
                return;
            }else {
                // this.$http.get('searchGoods/'+msg).then(res=>{
                //     console.log(res.data);
                // });
                let routeData = this.$router.resolve({ path: '/clientSearchGoods', query: {  searchmsg: msg } });
                window.open(routeData.href, '_blank');
            }
        },
        checkGood(id1) {
            
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            var f = false;
            this.$http.get('clientGoodInfo/'+id1).then(res=>{
                if(res.data.gname == '00000') {
                    alert('该商品已售罄');
                    f = true; 
                }
                if(f == false) {
                    let routeData = this.$router.resolve({ path: '/clientGoodInfo', query: {  id: id1 } });
                    window.open(routeData.href, '_blank');
                }else {
                    this.$router.go(0);
                }
            
            
            });
            
            
            
            
        },
        quitUser(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("转到登录");
              this.$router.go(0);   
          }else{
              alert("用户:"+window.localStorage.getItem('loginUser')+"即将注销!");
            window.localStorage.clear();
            
            this.$router.go(0);
            this.$message.success("注销成功!");
          }
            
        },
        enterShopcar(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
            let routeData = this.$router.resolve({ path: '/clientShopCar', query: {  username: this.username } });
            window.open(routeData.href, '_blank');
        },
        enterOrder(){
            if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
              return;
            }
          
            // this.$router.push('clientUserOrder');
            let routeData = this.$router.resolve({ path: '/clientUserOrder', query: {  username: this.username } });
            window.open(routeData.href, '_blank');
        }
    },
    created() {
        if(!(window.localStorage.getItem('loginUser')!=null)) {
              alert("尚未登录");
              this.$router.push('login');
             return;
          }
        this.$http.get('clientGoods').then(res=>{
            console.log(res.data);
            this.goodArr = res.data;
            this.username = window.localStorage.getItem('loginUser');
        });
    }
}
</script>
